import React from 'react'
import Header from './Header'
import Footer from './Footer'
import MisFotos from './MisFotos'

const MiGaleria = () => {
  return (
    <div>
        <Header/>
        <hr />
        <MisFotos/>
        <hr />
        <Footer/>
    </div>
  )
}

export default MiGaleria