import React from 'react'
import '../estilos/articulos.css';

const Articulos = () => {
  return (
    <div className='art-cont'>
        <img className="profi" src="images/profilaxis1.jpg" alt=""/>
        <p className="texto"><span>La profilaxis o limpieza dental</span> se debe realizar cada 6 meses precisamente para evitar la <cite>gingivitis o inflamacin de las encas</cite>, o una enfermedad periodontal causada por el clculo sarro que se va formando entre los dientes o a nivel de los cuellos de los dientes, y otras debajo de la enca en la cual los pacientes no perciben ese cmulo de sarro.</p>
        <hr />
        <img className="radix" src="images/radix1.jpg" alt=""/>
        <p className="texto"><span>Radix Entomolaris</span> El primer molar mandibular puede presentar diversas variaciones anatomicas. La mayora de los primeros molares mandibulares caucsicos presentan dos races, con dos conductos mesiales y uno distal. La principal variacin anatmica del primer molar mandibular es el Radix entomolaris, que se describe como un diente con una raz adicional distolingual</p>
        <hr />
        <img className="endo" src="images/endox.png" alt=""/>
        <p className="texto"><span>Las Endodoncias</span> se realizan en piezas dentales con caries profundas, que propician la inflamacin o la necrosis (muerte) de la pulpa dental. Otros motivos para la aparicin de pulpitis o inflamacin de la pulpa son algunos traumatismos, la abrasin, la erosin y el desgaste de los dientes por el roce entre ellos (por ejemplo debido al bruxismo) o la forma en que se realizan algunos tratamientos restauradores y los materiales que se utilizan en los mismos. La endodoncia es, como hemos dicho, la extirpacin total de la pulpa o nervio del diente. Se trata de un procedimiento de limpieza del sistema de conductos radiculares en el que se eliminan bacterias y tejido necrtico para dejar el conducto lo ms asptico posible.</p>
        <hr />
        <img className="endoned" src="images/endoned.jpg" alt=""/>
        <p className="texto"><span>Como se si necesito una endodoncia?</span> Sintomas: Hipersensibilidad  a los alimentos fros y calientes, Dolor al masticar, Debido a un traumatismo cada o golpe, Caries profunda, Decoloracin del Diente, Diente roto o astillado, Hinchazon de las encias en la zona adyacente al diente con caries.  El principal objetivo de la endodoncia es evitar que haya que extraer la pieza dental y eliminar cualquier resto de infeccin para dejar el diente completamente sano.</p>

    </div>
  )
}

export default Articulos